  .landing-app-section .phone-wrapper {
    margin: auto; }
    .landing-app-section .phone-wrapper .landing-app-section-magician {
      position: absolute;
      margin-left: -30px;
      z-index: -10;
      margin-top: 100px; }
      @media screen and (max-width: 980px) {
        .landing-app-section .phone-wrapper .landing-app-section-magician {
          margin-top: -25px; } }
      @media screen and (max-width: 500px) {
