          width: 120px; } }
    .landing-app-section .phone-wrapper .landing-app-section-bg {
      position: absolute; }
    @media screen and (max-width: 820px) {
      .landing-app-section .phone-wrapper {
        transform: translateX(calc(100vh * 0.04)); } }
    @media screen and (max-width: 768px) {
      .landing-app-section .phone-wrapper {
        transform: translateX(calc(100vh * 0.03)); } }
    @media screen and (max-width: 500px) {
      .landing-app-section .phone-wrapper {
        transform: translateX(10px);
        transition: all 0.4s ease-in-out;
        width: calc(90vw - 100px) !important; } }
    .landing-app-section .phone-wrapper .landing-app-section-phone {
      z-index: 3;
      width: calc(350px - (1080px - 100vh) * (1 / 1080) * 300);
      border-radius: calc(50px - (1080px - 100vh) * 0.05);
      /* @media screen and (max-width: 768px){
            width: 350px;
            border-radius: 50px;
          }*/ }
      @media screen and (max-width: 500px) {
        .landing-app-section .phone-wrapper .landing-app-section-phone {
          transition: all 0.4s ease-in-out;
          width: calc(90vw - 100px) !important;
          border-radius: calc(50px + (200px - 100vw) * 0.02); } }
      @media screen and (max-width: 420px) {
        .landing-app-section .phone-wrapper .landing-app-section-phone {
          border-radius: calc(50px - 100vw * 0.04); } }
      @media screen and (max-width: 350px) {
        .landing-app-section .phone-wrapper .landing-app-section-phone {
          border-radius: calc(50px - 100vw * 0.06); } }
      @media screen and (max-width: 300px) {
