          border-radius: calc(50px - 100vw * 0.18); } }
    .landing-app-section .phone-wrapper .phone-carousel-container {
      z-index: -1;
      position: absolute;
      height: 100%;
      width: 100%; }
      @media screen and (max-width: 500px) {
        .landing-app-section .phone-wrapper .phone-carousel-container {
          transition: all 0.4s ease-in-out;
          width: calc(90vw - 100px) !important; } }
      .landing-app-section .phone-wrapper .phone-carousel-container .control-arrow {
        display: none; }
      .landing-app-section .phone-wrapper .phone-carousel-container .control-dots {
        padding: 0;
        cursor: pointer;
        bottom: 40% !important;
        margin-left: -30px; }
        .landing-app-section .phone-wrapper .phone-carousel-container .control-dots .dot {
          border: solid 2px #ffffff;
          width: 10px;
          height: 10px;
          background-color: #46737A;
          border-radius: 50%;
          box-shadow: none;
          display: block !important;
          margin: 8px 0px !important; }
          @media screen and (max-width: 400px) {
            .landing-app-section .phone-wrapper .phone-carousel-container .control-dots .dot {
              width: 15px !important;
              height: 15px !important; } }
        .landing-app-section .phone-wrapper .phone-carousel-container .control-dots .selected {
          background-color: #00d7af; }
      .landing-app-section .phone-wrapper .phone-carousel-container .carousel {
        height: inherit !important;
        overflow: visible; }
      .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper {
        height: 100% !important;
        border-radius: 50px; }
        @media screen and (max-width: 500px) {
          .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper {
            transition: all 0.4s ease-in-out;
            width: calc(90vw - 100px) !important;
            border-radius: calc(50px + (200px - 100vw) * 0.02); } }
        @media screen and (max-width: 420px) {
          .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper {
            border-radius: calc(50px - 100vw * 0.04); } }
        @media screen and (max-width: 350px) {
          .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper {
            border-radius: calc(50px - 100vw * 0.06); } }
        @media screen and (max-width: 300px) {
          .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper {
            border-radius: calc(50px - 100vw * 0.1); } }
        .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper .slider {
          height: inherit !important; }
        .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper .slide {
          height: inherit !important;
          background-color: rgba(0, 0, 0, 0);
          padding: calc(15px - (1080px - 100vh) * (1 / 1080) * 10); }
          @media screen and (max-width: 768px) {
            .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper .slide {
              padding: 15px; } }
          @media screen and (max-width: 500px) {
            .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper .slide {
              padding: calc(1vw * 2.5);
              transition: all 0.4s ease-in-out;
              width: calc(90vw - 100px) !important;
              border-radius: calc(50px + (200px - 100vw) * 0.02); } }
          @media screen and (max-width: 420px) {
            .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper .slide {
              border-radius: calc(50px - 100vw * 0.04); } }
          @media screen and (max-width: 350px) {
            .landing-app-section .phone-wrapper .phone-carousel-container .slider-wrapper .slide {
              border-radius: calc(50px - 100vw * 0.06); } }
      .landing-app-section .phone-wrapper .phone-carousel-container .phone-images {
        height: inherit !important; }
        .landing-app-section .phone-wrapper .phone-carousel-container .phone-images img {
          height: inherit !important;
          object-fit: contain; }
